<template>
	<view class="box">
		<view class="team-content">
			<view class="basic-information">
				<view class="title">基本信息</view>
				<view class="basic">
					<view class="first-image">
						<view class="text shoutu">首图 <text>*</text></view>
						<uni-file-picker class="upload" @select="handleImage($event,0)" @delete="handleDeleteImage"
							:limit="1" :auto-upload="true" :del-icon="false">
						</uni-file-picker>
						<!-- <view class="count-upload-wrap">
							<view class="upload-wrap" v-for="item,index in imageList" @click="handleImage(index,0)">
								<image v-show="item" :src="`https://mesapi.nbyash.com/${item}`" mode="aspectFit">
								</image>
							</view>
							<view class="upload-wrap" v-if='imageList.length < 3 '>
								<view class="upload-tip flex flex-direction c-center j-center "
									@click="handleImage(-1,0)">
									<image class="icon-add" src="@/static/img/icon_photo@2x.png" mode="aspectFill">
									</image>
								</view>
							</view>
						</view> -->
					</view>
					<view class="flex-two">
						<view class="text">标题<text>*</text></view>
						<view class="picker">
							<input class="input biaoti" v-model="formData.Title" maxlength="20" type="text"
								placeholder="不超过20个字">
						</view>
					</view>
					<view class="flex-two">
						<view class="text">活动地点<text>*</text></view>
						<view class="picker">
							<input class="input" v-model="formData.ActivityAddress" type="text" @click="handleMap"
								disabled="true" placeholder="请选择活动地点">
							<image src="@/static/img/icon_go@3x.png" mode=""></image>
						</view>
					</view>
					<view class="flex-two">
						<view class="text">活动日期<text>*</text></view>
						<view class="picker">
							<picker mode="date" :value="dateTime"
								:start="dateList.length === 0?startDate:dateList[dateList.length-1].Time" :end="endDate"
								@change="bindDateChange">
								<input class="input" type="text" v-model="dateTime" disabled="true"
									placeholder="选择日期（可多选）">
							</picker>
							<image src="@/static/img/icon_go@3x.png" mode=""></image>
						</view>
					</view>
					<view class="flex-two">
						<view class="text">活动时间<text>*</text></view>
						<view class="picker">
							<picker mode="time" :value="formData.ActivityTime_Strat" @change="bindStartTimeChange">
								<!-- start="09:01" end="21:01" -->
								<input class="inputs" type="text" placeholder="开始时间" disabled="true"
									v-model="formData.ActivityTime_Strat">
							</picker>
							<text>-</text>
							<picker mode="time" :value="formData.ActivityTime_End" :start="formData.ActivityTime_Strat"
								@change="bindEndTimeChange">
								<input class="inputs" type="text" placeholder="结束时间" disabled="true"
									v-model="formData.ActivityTime_End">
							</picker>
							<image src="@/static/img/icon_go@3x.png" mode=""></image>
						</view>
					</view>
					<view class="flex-two">
						<view class="text">取消报名截止时间<text>*</text></view>
						<view class="picker">
							<picker :range="array" :value="formData.RegistrationDadlineTime"
								@change="handleArrayChange">
								<input class="input quxiao" type="text" placeholder="取消报名截止时间" disabled="true"
									v-model="array[formData.RegistrationDadlineTime]">
							</picker>
							<image src="@/static/img/icon_go@3x.png" mode=""></image>
						</view>
					</view>
					<view class="flex-three">
						<view class="text">活动描述<text></text></view>
						<view class="textarea">
							<textarea name="" id="" cols="30" v-model="formData.Content" rows="10"
								placeholder="请输入活动相关说明"></textarea>
						</view>
					</view>
					<view class="flex-three">
						<view class="text">添加照片<text>(最多6张)</text></view>
						<uni-file-picker class="upload" @select="handleImage($event,1)" @delete="handleDeleteImageList"
							:limit="6" :auto-upload="true" :del-icon="false">
						</uni-file-picker>
					</view>
				</view>
			</view>
			<view class="activity-money">
				<view class="title">活动费用信息</view>
				<view class="money">
					<view class="flex-money">
						<view class="text">收费类型<text>*</text></view>
						<view class="picker">
							<picker :range="activityType" :value="formData.Type" @change="handleTypeChange">
								<input class="input" type="text" placeholder="收费" disabled="true"
									v-model="activityType[formData.Type]">
							</picker>
							<image src="@/static/img/icon_go@3x.png" mode=""></image>
						</view>
					</view>
				</view>
			</view>
			<view class="team-list">
				<view class="" v-for="(item,index) in groupList">
					<view class="title-top">
						<view class="team">小组{{index+1}}</view>
						<view class="delect" @click="handleDelect(index)">
							<image src="@/static/img/icon_delect@2x.png" mode=""></image>
							<view class="">删除</view>
						</view>
					</view>
					<view class="content">
						<view class="flex-team">
							<view class="text">小组名称<text>*</text></view>
							<view class="picker">
								<input class="input" v-model="item.GroupName" type="text" placeholder="请输入小组名称">
							</view>
						</view>
						<view class="flex-textarea">
							<view class="text">小组描述</view>
							<view class="picker">
								<textarea class="" v-model="item.GroupDescribe" type="text"
									placeholder="请输入活动相关说明"></textarea>
							</view>
						</view>
						<view class="flex-team">
							<view class="text">报名人数<text>*</text></view>
							<view class="picker">
								<input class="input" v-model="item.SignNumber" type="text"
									placeholder="请输入本组报名人数"><text>人</text>
							</view>
						</view>
						<view class="flex-team">
							<view class="text">活动费用<text>*</text></view>
							<view class="picker">
								<input class="input" v-model="item.Price" type="text"
									placeholder="请输入本组活动费用"><text>元</text>
							</view>
						</view>
					</view>
				</view>
				<view class="">

				</view>
				<view class="add-team">
					<view class="add" @click="addTeam">
						<image src="@/static/img/icon_add@3x.png" mode=""></image>
						<view class="team-text">添加小组</view>
					</view>
				</view>
			</view>
		</view>

		<view class="submit">
			<view class="submit-text" @click="submit">
				发布
			</view>
		</view>
	</view>
</template>

<script>
	import {
		PostActivityCreateAjax,
		PostUpdateForImageAjax,
		GetGetImageUrlAjax
	} from '@/apis/api/modules/activity'
	import {
		validateItems
	} from './verification-item.ts'

	// import imgUpload from '@/frame/components/img-upload'
	import {
		token
	} from '@/apis/api/index.ts'
	export default {
		// components: {
		// 	imgUpload
		// },
		data() {
			const currentDate = this.getDate({
				format: true
			})
			return {
				array: ['活动前一小时', '活动前二小时', '活动前三小时', '活动前四小时', '活动前五小时', '活动前六小时'],
				arrayIndex: '',
				endTime: '',
				activityType: ['免费', '收费'],
				typeIndex: '',
				groupList: [{ //小组数据
					GroupName: '',
					GroupDescribe: '',
					SignNumber: '',
					Price: ''
				}],
				imageList: [], //照片合集
				dateList: [], //活动日期合集
				dateTime: "", //显示活动日期合集
				formData: {
					Title: '',
					Image: '',
					ActivityAddress: '', //活动地址
					Longitude: '', //经度
					Latitude: '', //纬度
					ActivityTime_Strat: '', //活动开始时间
					ActivityTime_End: '', //活动结束时间
					RegistrationDadlineTime: null, //报名截止时间
					Content: '', //活动描述，
					Type: null, //收费类型
				},
			}
		},
		computed: {
			startDate() {
				return this.getDate('start');
			},
			endDate() {
				return this.getDate('end');
			}
		},
		methods: {
			handleMap() {
				console.log('111')
				let that = this
				uni.chooseLocation({
					success: function(res) {
						// console.log('111', that.formData)
						// console.log('111', res.address)
						that.formData.ActivityAddress = res.address
						that.formData.Latitude = res.latitude //纬度
						that.formData.Longitude = res.longitude //径度
						// console.log(that.formData.ActivityAddress, that.formData.Latitude, that.formData
						// 	.Longitude)
					},
					fail: (res) => {
						// console.log(res)
					}
				});
			},
			handleArrayChange(e) {
				// this.arrayIndex = e.detail.value
				this.formData.RegistrationDadlineTime = e.detail.value
			},
			bindDateChange: function(e) {
				this.dateList.push({
					Time: e.detail.value
				})
				// console.log('dateList', this.dateList)
				this.dateTime = this.dateList.map(item => {
					return item.Time
				}).toString()
				// console.log('dateTime', this.dateTime)
			},
			bindStartTimeChange(e) {
				this.formData.ActivityTime_Strat = e.detail.value
			},
			bindEndTimeChange(e) {
				this.formData.ActivityTime_End = e.detail.value
			},
			handleTypeChange(e) {
				this.typeIndex = e.detail.value
				this.formData.Type = e.detail.value
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			/**
			 * 上传图片
			 * @param {Object} img
			 * @param {Object} upType
			 */
			handleImage(img, upType) {
				// const formData = new FormData()
				// formData.append('type', 0)
				const time = (new Date()).getTime()
				uni.uploadFile({
					url: `https://farm.juzhentech.com/api/Activity/UpdateForImage?type=${upType}&code=${time}`,
					header: {
						'Authorization': token.value,
						// 'Content-Type': 'application/json;charset=utf-8'
						// 'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
						// 'Content-Type': 'multipart/form-data;charset=utf-8'
					},
					filePath: img.tempFilePaths[0],
					name: 'file',
					formData: {
						"type": upType
					},
					success: async (uploadFileRes) => {
						// console.log('zhaipian', uploadFileRes)
						const result = await GetGetImageUrlAjax({
							code: time,
							token: true
						})
						let image = result.Result
						if (upType === 0) {
							this.formData.Image = image
						} else {
							this.imageList.push({
								Image: image
							})
						}

						// console.log('zhaipian', result)
						// console.log('imageList', this.imageList)
					}
				});
			},
			handleDeleteImage(e) {
				console.log('handleDeleteImage', e)
			},
			handleDeleteImageList(e) {

				console.log('handleDeleteImageList', e)
			},
			addTeam() {
				this.groupList.push({
					GroupName: '',
					GroupDescribe: '',
					SignNumber: '',
					Price: ''
				})
			},
			handleDelect(index) {
				this.groupList.splice(index, 1)
			},
			/**
			 * 判断数据是否填写
			 */
			validateForm() {
				if (!this.validForm(this.formData, [{
							key: "Image",
							text: "请上传首图"
						}, {
							key: "Title",
							text: "请输入标题"
						},
						{
							key: "ActivityAddress",
							text: "请选择活动地址"
						},
						{
							key: "ActivityTime_Strat",
							text: "请输入活动开始时间"
						},
						{
							key: "ActivityTime_End",
							text: "请选择活动结束时间"
						},
						{
							key: "RegistrationDadlineTime",
							text: "请选择活动报名截止时间"
						},
						{
							key: "Type",
							text: "请选择收费类型"
						},

					])) {
					return false
				}
				if (!this.dateList === 0) {
					uni.showToast({
						title: '请选择活动日期',
						icon: 'none'
					})
					return false
				}
				if (this.groupList.length === 0) {
					uni.showToast({
						title: '请填写小组数据',
						icon: 'none'
					})
					return false
				}
				const validateResult = validateItems(
					this.groupList,
					function(item, rowIndex) {
						const rowNumber = rowIndex + 1
						const rowText = `[第${rowNumber}组]`

						if (!item.GroupName) {
							return '请输入小组名称' + rowText
						}
						if (!item.SignNumber) {
							return '请输入本组报名人数' + rowText
						}
						if (!item.Price) {
							return '请输入本组活动费用' + rowText
						}
						return false
					}
				)
				if (!validateResult.ok) {
					uni.showToast({
						title: validateResult.msg,
						icon: 'none'
					})
					return false
				}
				return true
			},
			/**
			 * 发布活动
			 */
			async submit() {
				// console.log(this.formData)
				if (!this.validateForm()) {
					return false
				}
				await PostActivityCreateAjax({
					...this.formData,
					ActiveDateList: this.dateList,
					ActivityImageList: this.imageList ? this.imageList : null,
					ActivityGroupList: this.groupList,
					token: true
				})
				uni.showToast({
					title: '发布成功！',
					icon: 'success'
				})
				setTimeout(() => {
					uni.navigateBack({
						delta: 1
					})
				}, 1000)
			}
		}
	}
</script>

